$background-color: #212020;
$font-stack: Arial, Helvetica, sans-serif;

@import './header';

body{
  background-color: $background-color;
  height:100%;
}

p {
  color: grey;
  margin-bottom: 0%;
  margin-top:0%;
}



.float-container {
    border: 3px solid #fff;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
}

.float-child {
    position: relative;
    width: 47%;
    height: 100%;
    float: left;
    border: 2px solid red;

}

#MainBox_Right {
  border: 1px solid green;
}

#MainBox_Left_Title {
  border: 1px solid green;
  width: 75%;
  height: 50%;
  text-align: left;
  float: right;
  font-size: 105%;
}

#MainBox_Left_Content {
  border: 1px solid green;
  width: 75%;
  height: 50%;
  text-align: left;
  float: right;
  font-size: 105%;
  line-height: 200%;
  word-spacing: 120%;
}
